<template>
    <div class="form">
        <div class="column">
            <span class="header">Zaloguj</span>
            <span class="error">{{l_message}}</span>
            <label for="l-email">Nazwa użytkownika</label>
            <input type="text"
                   name="email"
                   id="l-email"
                   v-model="LUsername"
            >
            <label for="l-password">Hasło</label>
            <input
                type="password"
                name="password"
                id="l-password"
                v-model="LPassword">
            <button @click="login">Zaloguj</button>
        </div>
        <div class="column">
            <span class="header">Rejestracja</span>
            <span class="error">{{r_message}}</span>
            <label for="r-username">Login</label>
            <input type="text"
                   name="username"
                    id="r-username"
                   v-model="RUsername"
            >
            <label for="r-first-name">Imie</label>
            <input type="text"
                   name="firstname"
                   id="r-first-name"
                   v-model="RFirstName"
            >
            <label for="r-last-name">Nazwisko</label>
            <input type="text"
                   name="lastname"
                   id="r-last-name"
                   v-model="RLastName"
            >
            <label for="r-email">E-mail</label>
            <input type="text"
                   name="email"
                   id="r-email"
                   v-model="REmail"
            >
            <label for="r-password">Hasło</label>
            <input
                type="password"
                name="password"
                id="r-password"
                v-model="RPassword"
            >
            <label for="r-password">Miejscowość</label>
            <input
                    type="text"
                    name="city"
                    id="r-city"
                    v-model="RCity"
            >
            <label for="r-password">Numer telefonu</label>
            <input
                    type="number"
                    name="phone"
                    id="r-phone"
                    v-model="RPhone"
            >
            <button @click="register">Rejestracja</button>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'
    import ENV from '../../env'
    export default {
        name: "Register.vue",
        data: ()=>{
            return {
                RUsername: null,
                RPassword: null,
                REmail: null,
                RFirstName:null,
                RLastName: null,
                RCity: null,
                RPhone: null,
                LUsername: null,
                LPassword: null,
                l_message: null,
                r_message: null
            }
        },
        methods:{
            login(){
                axios.post(ENV.host+'login/',
                    {
                        username: this.LUsername,
                        password: this.LPassword
                    }).then(response => {
                        if(response.data.status===false){
                            this.l_message=response.data.message
                        }
                        if(response.data.status===true){
                            this.$cookies.set('api_token', response.data.api_token)
                            this.$cookies.set('user_id', response.data.user_id)
                            this.$cookies.set('user_admin', response.data.user_admin)
                            this.$router.push({name: 'Home'})
                        }
                })
            },
            register(){
                axios.post(ENV.host+'user/register/',
                    {
                        email: this.REmail,
                        username: this.RUsername,
                        password: this.RPassword,
                        firstname: this.RFirstName,
                        lastname: this.RLastName,
                        phone: this.RPhone,
                        city: this.RCity
                    }).then(response =>{
                        if(response.data.status===false){
                            this.r_message=response.data.message
                        }
                        if(response.data.status===true){
                            this.r_message="Konto utworzone, możesz się zalogować"
                        }
                })
            }
        }
    }
</script>

<style scoped>
    .form{
        background-color: #fff;
        padding: 2rem;
        display: flex;
        justify-content: center;
        text-align: left;
        margin-top: 3rem;
    }

    .form input, .form label{
        display: block;
        width: calc(100% - 1rem - 2px);
    }

    .column{
        width: 20vw;
        margin: 0 3rem;
    }

    .header{
        font-size: 1.2em;
        font-weight: bold;
    }

    input{
        margin-bottom: 10px;
        border: 1px solid #DE5B63;
        padding: 0.5rem;
    }

    button{
        background-color: red;
        border: 1px solid #DE5B63;
        padding: 0.5rem 1.5rem;
        color: #fff;
        font-weight: bold;
        float: right;
        border-radius: 7px;
    }
    .error{
        display: block;
        color: red;
    }
</style>