<template>
    <div class="list">
        <div class="users" v-if="this.$cookies.get('user_admin')">
            <table cellspacing="0">
                <tr>
                    <th>ID</th>
                    <th>Nazwa użytkownika</th>
                    <th>Imie Nazwisko</th>
                    <th>Numer telefonu</th>
                    <th>Admin</th>
                    <th>Opcje</th>
                </tr>
                <tr v-for="user in users" :key="user.id">
                    <td>{{user.id}}</td>
                    <td>{{user.username}}</td>
                    <td>{{user.firstName}} {{user.lastName}}</td>
                    <td>{{user.phoneNumber}}</td>
                    <td><input type="checkbox" name="admin" v-model="user.admin" @click="setAdmin(user.id, !user.admin)"></td>
                    <td><button class="btn" @click="remove(user.id)">Usuń</button></td>
                </tr>
            </table>
        </div>
        <div v-if="!this.$cookies.get('user_admin')" class="access-denied">
            <p>Brak uprawnień</p>
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    import ENV from "../../env";

    export default {
        name: "Users",
        data: () => {
            return {
                users: [
                    {
                        id: 1,
                        username: 'username',
                        firstName: 'firstname',
                        lastName: 'lastname',
                        phoneNumber: '123456789',
                        admin: false
                    }
                ]

            }
        },
        mounted(){
            this.getData();
        },
        methods: {
            setAdmin(userId, privileges){
                axios.put(ENV.host+'user/set-admin/'+userId,
                    {privileges: privileges},
                    {headers: {api_token: this.$cookies.get("api_token")}})
                    .then(response=>{
                        if(!response.data.status){
                            alert(response.data.message)
                        }
                    })
            },
            remove(userId){
                axios.delete(ENV.host+'user/'+userId,
                    {headers: {api_token: this.$cookies.get("api_token")}})
                .then(response=>{
                    if(!response.data.status){
                        alert(response.data.message)
                    }else{
                        this.getData()
                    }
                })
            },
            getData(){
                axios.get(ENV.host+'user/',
                    {headers: {api_token: this.$cookies.get("api_token")}})
                    .then(response=>{this.users=response.data.users})
            }
        }
    }
</script>

<style scoped>
    .list {
        margin: 1px 0;
    }

    .users {
        background-color: white;
        padding: 2rem 15vw;
        min-height: 60vh;
    }

    table {
        width: 100%;
    }

    tr {
        border-bottom: 1px solid red;
    }
    .access-denied{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        background-color: white;
        min-height: 60vh;
    }

</style>