<template>
    <div class="navbar">
        <div class="main">
            <router-link to="/" class="brand">Serwis ogłoszeń</router-link>
            <div class="links">
                <router-link to="/new-offer" v-if="authenticated">Dodaj ogłoszenie</router-link>
                <a v-if="authenticated" @click="toggleSettings">
                    {{username}}
                </a>
                <router-link to="/register" v-if="!authenticated">Logowanie</router-link>
            </div>
        </div>
        <div class="settings" v-if="showSettings">
            <router-link to="/waiting-offers" v-if="admin">Oczekujące oferty</router-link>
            <router-link to="/users" v-if="admin">Lista użytkowników</router-link>
            <a @click="logout">Wyloguj</a>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'
    import ENV from '../../env'

    export default {
        name: "navbar",
        data: function () {
            return {
                authenticated: false,
                username: "",
                admin: false,
                showSettings: false
            }
        },
        methods: {
            toggleSettings() {
                this.showSettings = !this.showSettings
            },
            logout() {
                this.authenticated = false;
                this.username = "";
                this.admin = false;
                this.showSettings=false;

                axios.get(ENV.host+"logout/",
                    {headers: {api_token: this.$cookies.get("api_token")}})
                .then(()=>{
                    this.$cookies.remove("api_token")
                    this.$cookies.remove("user_id")
                    this.$cookies.remove("user_admin")
                    if(this.$route.name!=='Home') this.$router.push({name: "Home"})
                })
            }
        },
        mounted(){
            if(this.$cookies.get("api_token")){
                axios.get(ENV.host+"session/",
                    {headers: {api_token: this.$cookies.get("api_token")}})
                .then(response=>{
                    this.authenticated=true;
                    this.username=response.data.username;
                    this.admin=response.data.admin;
                })
            }
        }
    }
</script>

<style scoped>
    .navbar .main {
        background-color: #fff;
        min-height: 4rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 2rem;
    }

    .navbar .settings {
        background-color: white;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        height: 2rem;
        border-top: 1px solid #888;
    }

    .brand {
        font-size: 1.5em;
    }

    .links {
        display: flex;

    }

    .links *, .settings * {
        margin: 0 10px;
    }

</style>