<script>
    import axios from 'axios'
    import ENV from '../../env'
    export default {
        name: 'Home',
        components: {},
        data: () => {
            return {
                newest: [
                    {
                        image: "https://i2.wp.com/zbiczno.pl/wp-content/uploads/2020/01/placeholder.png?fit=1024%2C683&ssl=1",
                        title: "Marka Model",
                        yearbook: "2020",
                        mileage: "123456",
                        price: "123456",
                        id: 1
                    },
                    {
                        image: "https://i2.wp.com/zbiczno.pl/wp-content/uploads/2020/01/placeholder.png?fit=1024%2C683&ssl=1",
                        title: "Marka Model",
                        yearbook: "2020",
                        mileage: "123456",
                        price: "123456",
                        id: 2
                    },
                    {
                        image: "https://i2.wp.com/zbiczno.pl/wp-content/uploads/2020/01/placeholder.png?fit=1024%2C683&ssl=1",
                        title: "Marka Model",
                        yearbook: "2020",
                        mileage: "123456",
                        price: "123456",
                        id: 3
                    },
                    {
                        image: "https://i2.wp.com/zbiczno.pl/wp-content/uploads/2020/01/placeholder.png?fit=1024%2C683&ssl=1",
                        title: "Marka Model",
                        yearbook: "2020",
                        mileage: "123456",
                        price: "123456",
                        id: 4
                    }
                ],
                filters: {
                    status: "all",
                    brand: null,
                    model: null,
                    yearbookSince: null,
                    yearbookTo: null,
                    priceSince: null,
                    priceTo: null,
                    mileageSince: null,
                    mileageTo: null,
                    fuel: null
                },
                brands: [
                    {
                        name: "Audi",
                        models: [
                            "A3",
                            "A4",
                            "A6"
                        ]
                    },
                    {
                        name: "BMW",
                        models: [
                            "Seria-1",
                            "Seria-3",
                            "Seria-5"
                        ]
                    },
                    {
                        name: "Alfa Romeo",
                        models: [
                            "Giulietta",
                            "MiTO",
                            "Giulia"
                        ]
                    }
                ]
            }
        },
        methods: {
            search() {
                this.$router.push({
                    name: 'offers',
                    params: this.filters
                })
            }
        },
        computed: {
            cModels() {
                if (this.filters.brand == null) {
                    return []
                } else {
                    return this.filters.brand.models
                }
            }
        },
        mounted() {
            axios.get(ENV.host+'ad/newest').then(response=>{this.newest=response.data.newest})
            axios.get(ENV.host+'ad/models').then(response=>{this.brands=response.data.models})
        }
    }
</script>
