<template>
    <div class="offer">
        <div class="no-accepted-alert" v-if="offer.accepted===false">
            <p>To ogłoszenie oczekuje na akceptacje</p>
            <div>
                <button v-if="this.$cookies.get('user_admin')" @click="accept">Zatwierdź</button>
                <button v-if="this.$cookies.get('user_admin')" @click="remove">Odrzuć</button>
            </div>
        </div>
        <div class="short-info">
            <div class="gallery">
                <div class="main">
                    <button @click="previewImage()" class="arrow"><img src="../../public/img/perv.png" alt="Poprzednie">
                    </button>
                    <img :src="offer.images[imageIndex]" :alt="offer.images[imageIndex]">
                    <button @click="nextImage" class="arrow"><img src="../../public/img/next.png" alt="Następne">
                    </button>
                </div>
                <div class="miniatures">
                    <img v-for="image in offer.images" :key="image" :src="image" :alt="image">
                </div>
            </div>
            <div class="text">
                <span class="header">{{offer.brand}} {{offer.model}}</span>
                <span class="localisation">{{offer.user.name}} (tel.{{offer.user.number}}), {{offer.user.city}}</span>
                <span class="price">{{offer.price}}zł</span>
                <button class="btn" v-if="showRemoveButton" @click="remove">Zamknij ogłoszenie</button>
            </div>
        </div>
        <div class="details">
            <div class="header">Szczegóły</div>
            <div class="content">
                <ul class="column">
                    <li>
                        <span class="parameter">Marka</span><span class="value">{{offer.brand}}</span>
                    </li>
                    <li>
                        <span class="parameter">Model</span><span class="value">{{offer.model}}</span>
                    </li>
                    <li>
                        <span class="parameter">Stan</span><span class="value">{{offer.status=='new'? 'Nowy': 'Używany'}}</span>
                    </li>
                    <li>
                        <span class="parameter">Rodziaj nadwozia</span><span class="value">{{offer.body}}</span>
                    </li>
                </ul>
                <ul class="column">
                    <li>
                        <span class="parameter">Paliwo</span><span class="value">{{offer.fuel=='pb'? 'Benzyna': offer.fuel=='on'? 'Olej napędowy': 'LPG'}}</span>
                    </li>
                    <li>
                        <span class="parameter">Pojemność</span><span class="value">{{offer.capacity}}ccm</span>
                    </li>
                    <li>
                        <span class="parameter">Przebieg</span><span class="value">{{offer.mileage}}km</span>
                    </li>
                </ul>
            </div>
        </div>
        <div class="description">
            <div class="header">Opis</div>
            <p>{{offer.description}}</p>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'
    import ENV from '../../env'

    export default {
        name: "Offer",
        data: function () {
            return {
                offer: {
                    accepted: true,
                    images: [
                        "https://onaliternote.files.wordpress.com/2016/11/wp-1480230666843.jpg",
                        "https://image.shutterstock.com/image-vector/example-free-grunge-retro-red-260nw-303212123.jpg",
                    ],
                    brand: "Example",
                    model: "CAR",
                    description: "\"Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.\"",
                    yearbook: "2020",
                    fuel: "Benzyna",
                    body: "Sedan",
                    capacity: "1596",
                    mileage: "123456",
                    status: "Nowy",
                    price: "123456",
                    user: {
                        name: "Janusz",
                        number: "123456789",
                        city: "Rzeszów",
                        userId: null
                    }
                },
                imageIndex: 0,
                showRemoveButton: false
            }
        },
        methods: {
            nextImage() {
                this.imageIndex++
                if (this.imageIndex > this.offer.images.length - 1) {
                    this.imageIndex = 0
                }
            },
            previewImage() {
                this.imageIndex--
                if (this.imageIndex < 0) {
                    this.imageIndex = this.offer.images.length - 1
                }
            },
            accept() {
                axios.put(ENV.host + 'ad/accept/' + this.$route.params.offerId,
                    {},
                    {headers: {api_token: this.$cookies.get("api_token")}})
                    .then(()=>{
                        this.$router.push({name: 'waitingOffers'})
                    })
            },
            remove() {
                axios.delete(ENV.host + 'ad/' + this.$route.params.offerId,
                    {headers: {api_token: this.$cookies.get("api_token")}})
                    .then(() => {
                        if(this.$cookies.get('user_admin')=='true')this.$router.push({name: 'waitingOffers'})
                        if(this.$cookies.get('user_admin')!='true')this.$router.push({name: 'Home'})
                    })
            }
        },
        mounted() {
            const offerId = this.$route.params.offerId;
            axios.get(ENV.host + 'ad/' + offerId).then(response => {
                this.offer = response.data.offer
                this.showRemoveButton=this.offer.user.userId==this.$cookies.get('user_id') || this.$cookies.get('user_admin')=='true'
            })
        }
    }
</script>

