<template>
    <div class="list">
        <div class="filters">
            <div class="column">
                <div class="model">
                    <select v-model="filters.brand">
                        <option :value="null">Marka pojazdu</option>
                        <option v-for="brand in brands" :key="brand.name" :value="brand">
                            {{brand.name}}
                        </option>
                    </select>
                    <select v-model="filters.model">
                        <option :value="null">Model pojazdu</option>
                        <option v-for="model in cModels" :key="model" :value="model">
                            {{model}}
                        </option>
                    </select>
                </div>
                <div class="mileage">
                    <input type="number" v-model="filters.mileageSince" placeholder="Przebieg od">
                    <input type="number" v-model="filters.mileageTo" placeholder="Przebieg do">
                </div>
            </div>
            <div class="column">
                <div class="yearbook">
                    <input type="number" v-model="filters.yearbookSince" placeholder="Rok produkcji od">
                    <input type="number" v-model="filters.yearbookTo" placeholder="Rok produkcji do">
                </div>
                <div class="fuel">
                    <select v-model="filters.fuel">
                        <option :value="null">Paliwo</option>
                        <option value="pb">Benzyna</option>
                        <option value="on">Olej napędowy</option>
                        <option value="lpg">LPG</option>
                    </select>
                    <select v-model="filters.status">
                        <option value="all">Wszystkie</option>
                        <option value="new">Nowe</option>
                        <option value="used">Używane</option>
                    </select>
                </div>
            </div>
            <div class="column">
                <div class="price">
                    <input type="number" v-model="filters.priceSince" placeholder="Cena od">
                    <input type="number" v-model="filters.priceTo" placeholder="Cena do">
                </div>
                <div class="search">
                    <button @click="search">Wyszukaj</button>
                </div>
            </div>
        </div>
        <div class="offers">
            <router-link class="offer" v-for="offer in offers" :key="offer.id" :to="{name: 'offer', params:{offerId: offer.id}}">
                <img :src="offer.image" :alt="offer.image" v-if="offer.image!=null">
                <div class="text">
                    <span class="header">{{offer.title}}</span>
                    <span v-if="offer.accepted===false" class="not-accepted">Oczekuje na akceptacje</span>
                    <p>{{offer.capacity}}cm3, {{offer.fuel=='pb'? 'Benzyna': offer.fuel=='on'? 'Olej napędowy': 'LPG'}},{{offer.mileage}}km</p>
                    <span class="price">{{offer.price}}zł</span>
                </div>
            </router-link>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'
    import ENV from '../../env'
    export default {
        name: "List",
        data: function () {
            return {
                offers: [
                    {
                        id: 1,
                        image: "https://i2.wp.com/zbiczno.pl/wp-content/uploads/2020/01/placeholder.png?fit=1024%2C683&ssl=1",
                        title: "Example Car",
                        brand: "Example",
                        model: "Car",
                        yearbook: "2020",
                        fuel: "Beznyna",
                        mileage: "123456",
                        capacity: "1596",
                        price: "123456",
                        accepted: true
                    },
                    {
                        id: 2,
                        image: "https://i2.wp.com/zbiczno.pl/wp-content/uploads/2020/01/placeholder.png?fit=1024%2C683&ssl=1",
                        title: "Example Car",
                        brand: "Example",
                        model: "Car",
                        yearbook: "2020",
                        fuel: "Beznyna",
                        mileage: "123456",
                        capacity: "1596",
                        price: "123456",
                        accepted: true
                    }
                ],
                filters: {
                    brand: null,
                    model: null,
                    mileageSince: null,
                    mileageTo: null,
                    priceSince: null,
                    priceTo: null,
                    yearbookSince: null,
                    yearbookTo: null,
                    fuel: null
                },
                brands: [
                    {
                        name: "Audi",
                        models: [
                            "A3",
                            "A4",
                            "A6"
                        ]
                    },
                    {
                        name: "BMW",
                        models: [
                            "Seria-1",
                            "Seria-3",
                            "Seria-5"
                        ]
                    },
                    {
                        name: "Alfa Romeo",
                        models: [
                            "Giulietta",
                            "MiTO",
                            "Giulia"
                        ]
                    }
                ]
            }
        },
        computed: {
            cModels() {
                if (this.filters.brand == null) {
                    return []
                } else {
                    return this.filters.brand.models
                }
            }
        },
        methods: {
            search() {
                this.filters=this.$route.params
                axios.post(ENV.host+'ad/',
                    {
                        brand: this.filters.brand!=null? this.filters.brand.name: null,
                        model: this.filters.model,
                        mileage_since: this.filters.mileageSince,
                        mileage_to: this.filters.mileageTo,
                        price_since: this.filters.priceSince,
                        price_to: this.filters.priceTo,
                        yearbook_since: this.filters.yearbookSince,
                        yearbook_to: this.filters.yearbookTo,
                        fuel: this.filters.fuel,
                        status: this.filters.status
                    },
                    {headers: {api_token: this.$cookies.get("api_token")}})
                    .then(response=>{this.offers=response.data.offers})
            }
        },
        mounted() {
            this.search()
            axios.get(ENV.host+'ad/models').then(response=>{this.brands=response.data.models})
        }
    }
</script>

