<template>
    <div class="new-offer">
        <span class="main-header header">Dodaj pojazd</span>

        <div class="type">

            <div class="radio-select">
                <input type="radio" id="new" name="type" value="new" v-model="offer.type">
                <label for="new">Nowe</label>
            </div>
        </div>

        <div class="type">

            <div class="radio-select">
                <input type="radio" id="used" name="type" value="used" v-model="offer.type">
                <label for="used">Używane</label>
            </div>
        </div>

        <div class="brand">
            <span class="header">Marka</span>
            <div class="inputs">
                <input type="text" class="filter-input" v-model="offer.brand">
            </div>
        </div>

        <div class="model">
            <span class="header">Model</span>
            <div class="inputs">
                <input type="text" class="filter-input" v-model="offer.model">
            </div>
        </div>

        <div class="yearbook">
            <span class="header">Rok produkcji</span>
            <div class="inputs">
                <input type="number" class="filter-input" v-model="offer.yearbook">
            </div>
        </div>

        <div class="price">
            <span class="header">Cena</span>
            <div class="inputs">
                <input type="number" class="filter-input" v-model="offer.price">
            </div>
        </div>

        <div class="mileage">
            <span class="header">Przebieg</span>
            <div class="inputs">
                <input type="number" class="filter-input" v-model="offer.mileage">
            </div>
        </div>

        <div class="fuel">
            <span class="header">Paliwo</span>
            <div class="inputs">
                <select select class="filter-input" v-model="offer.fuel">
                    <option value="pb">Benzyna</option>
                    <option value="on">Olej napędowy</option>
                    <option value="lpg">LPG</option>
                </select>
            </div>
        </div>

        <div class="power">
            <span class="header">Moc silnika</span>
            <div class="inputs">
                <input type="number" class="filter-input" v-model="offer.power">
            </div>
        </div>

        <div class="engine-capacity">
            <span class="header">Pojemność silnika</span>
            <div class="inputs">
                <input type="number" class="filter-input" v-model="offer.engine_capacity">
            </div>
        </div>

        <div class="body-type">
            <span class="header">Rodzaj nadwozia</span>
            <div class="inputs">
                <select select class="filter-input" v-model="offer.body_type">
                    <option>sedan</option>
                    <option>hatchback</option>
                    <option>kombi</option>
                    <option>van</option>
                    <option>terenowy</option>
                    <option>coupe</option>
                </select>
            </div>
        </div>

        <div class="additional-info">
            <span class="header">Opis</span>
            <textarea class="description" v-model="offer.description"></textarea>
        </div>

        <div class="images-wrapper">
            <div class="images">
                <img v-for="image in offer.images" :key="image" :src="image">
            </div>
            <div class="form">
                <input type="text" placeholder="Wpisz link do obrazu" class="filter-input" v-model="image">
                <br>
                <button class="btn" @click="addImage">Dodaj</button>
            </div>
        </div>

        <button class="btn" @click="save">Dodaj</button>
    </div>
</template>

<script>
    import axios from "axios";
    import ENV from "../../env";

    export default {
        name: "NewOffer",
        data: function () {
            return {
                offer: {
                    type: null,
                    brand: null,
                    model: null,
                    yearbook: null,
                    price: null,
                    mileage: null,
                    fuel: null,
                    description: null,
                    body_type: null,
                    engine_capacity: null,
                    power: null,
                    images: []
                },
                image: null
            }
        },
        methods: {
            addImage(){
                if(this.image==null){
                    return null
                }
                this.offer.images.push(this.image)
                this.image=null
            },
            save(){
                console.log(this.offer)
                axios.post(ENV.host+"ad/store/",
                    this.offer,
                    {headers: {api_token: this.$cookies.get("api_token")}})
                    .then(response=>{
                        console.log(response.data)
                        this.$router.push({name: 'offer', params: {offerId: response.data.offer_id}})
                    })
            }
        }
    }
</script>

